:root {
	--bg: #161616;
	--bg-alt: #d93f3f;
	--fg: white;
}

@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap");

body,
html {
	background-color: var(--bg);
	color: var(--fg);
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
	margin: 0;
	padding: 0;
	font-family: "JetBrains Mono", monospace;
}

.box {
	position: relative;
	z-index: 2;
	background-color: var(--bg-alt);
	display: flex;
	justify-content: center;
	border-radius: 1rem;
	padding: 1rem;
	flex-direction: column;
}

h1 {
	padding: 0rem 0;
	margin: 0;
	text-align: center;
	text-decoration: underline;
}

.box a {
	text-decoration: none;
	color: var(--bg);
	text-align: center;
}

.box a:hover {
	color: var(--fg);
}

.box:hover {
	box-shadow: 1px 1px 10px var(--bg-alt);
}
